"use client";

import { useId, useState } from "react";
import type { SupportedLocale } from "@/data/translations";

export function TcpaConsent({
  privacyHref,
  termsHref,
  onChange,
  locale = "en",
}: {
  privacyHref: string;
  termsHref: string;
  onChange?: (accepted: boolean) => void;
  locale?: SupportedLocale;
}) {
  const checkboxId = useId();
  const [accepted, setAccepted] = useState(false);

  const copy = locale === "es"
    ? {
        consentPrefix:
          "Al enviar, aceptas que nosotros y nuestros socios de préstamos te contactemos al teléfono y correo proporcionados, incluso mediante marcadores automáticos y mensajes pregrabados.",
        consentSuffix:
          "El consentimiento no es condición de compra. Responde STOP para darte de baja.",
        privacy: "Política de Privacidad",
        terms: "Términos",
        compliance:
          "El sello de tiempo, dirección IP, agente de usuario y hash del texto de consentimiento se almacenan para cumplimiento. Puedes revocar el consentimiento en cualquier momento contactando soporte o respondiendo STOP.",
      }
    : {
        consentPrefix:
          "By submitting, you agree to be contacted by us and our lending partners at the phone and email you provided, including by autodialer and pre-recorded messages.",
        consentSuffix:
          "Consent is not a condition of purchase. Reply STOP to opt out.",
        privacy: "Privacy Policy",
        terms: "Terms",
        compliance:
          "The timestamp, IP address, user agent, and consent text hash are stored for compliance. You can revoke consent at any time by contacting support or replying STOP.",
      };

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = event.target.checked;
    setAccepted(value);
    onChange?.(value);
  };

  return (
    <div className="space-y-3 rounded-2xl border border-slate-200 bg-white/80 p-4 text-xs text-slate-500 shadow-inner">
      <label className="flex items-start gap-3">
        <input
          id={checkboxId}
          type="checkbox"
          checked={accepted}
          onChange={handleChange}
          className="mt-1 size-4 rounded border-slate-300 text-blue-500 focus:ring-blue-400"
        />
        <span>
          {copy.consentPrefix} {copy.consentSuffix} {locale === "es" ? "Consulta" : "See our"}
          {" "}
          <a className="text-blue-600 underline" href={privacyHref}>
            {copy.privacy}
          </a>
          {" "}
          {locale === "es" ? "y" : "and"}
          {" "}
          <a className="text-blue-600 underline" href={termsHref}>
            {copy.terms}
          </a>
          .
        </span>
      </label>
      <p className="text-[11px] text-slate-400">
        {copy.compliance}
      </p>
    </div>
  );
}
